body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

footer {
  font-size: 70%;
  text-align: center;
  color: #747474;
  padding-bottom: 5px; }
  footer a {
    text-decoration: none;
    color: #9e9e9e; }
  footer a:visited {
    text-decoration: none; }

.MainWindow {
  width: 800px;
  height: 800px;
  display: -webkit-flex;
  display: flex;
  margin: auto;
  margin-top: 50px; }
  .MainWindow * {
    box-sizing: border-box; }
  .MainWindow .LeftPanel {
    width: 150px; }
    .MainWindow .LeftPanel .OperationsPanel {
      height: 200px; }
    .MainWindow .LeftPanel .PartsPanel {
      height: 600px; }
      .MainWindow .LeftPanel .PartsPanel .parts-row {
        display: -webkit-flex;
        display: flex; }
        .MainWindow .LeftPanel .PartsPanel .parts-row .Part {
          width: 35px;
          height: 35px;
          color: #64867a; }
          .MainWindow .LeftPanel .PartsPanel .parts-row .Part.selected {
            background-color: #3a3f48; }
  .MainWindow .RightPanel {
    width: 650px; }
    .MainWindow .RightPanel .TopInfoPanel {
      height: 100px; }
    .MainWindow .RightPanel .ReactorPanel {
      min-height: 700px; }
      .MainWindow .RightPanel .ReactorPanel .reactor-row {
        display: -webkit-flex;
        display: flex; }
        .MainWindow .RightPanel .ReactorPanel .reactor-row:first-child > .ReactorCell {
          border-top: 2px solid darkgray; }
        .MainWindow .RightPanel .ReactorPanel .reactor-row .ReactorCell {
          width: 35px;
          height: 35px;
          border-bottom: 2px solid darkgray;
          border-right: 2px solid darkgray;
          color: #988871; }
          .MainWindow .RightPanel .ReactorPanel .reactor-row .ReactorCell:first-child {
            border-left: 2px solid darkgray; }
  .MainWindow .TopInfoPanel .part-title {
    color: #fffbb7;
    font-weight: bold;
    padding-bottom: 5px; }
  .MainWindow .TopInfoPanel .part-desc {
    color: #ffffff; }

